var shopid=0;
import skyRaty from "../../components/skyraty.vue";
export default {
	components:{
		skyRaty
	},
	data: function() {
		return {
			pageData: {},
			pageLoad: false,
			ksid:0,
			ksid1:0,
			ksList:[],
			ksList2:[],
			price:0,
			cart_amount:0
		}
	},
	onLoad:function(ops){
		this.getPage(ops.id);
	},
	methods: {
		goGuest:function(id,shopid){
			uni.navigateTo({
				url:"../b2b_guest/user?shopid="+shopid+"&id="+id
			})
		},
		goShop:function(shopid){
			uni.navigateTo({
				url:"../b2b_shop/index?shopid="+shopid
			})
		},
		getPage: function(id) {
			var that = this;
			uni.request({
				url: that.app.apiHost + "/module.php?m=b2b_product&a=show&ajax=1&id=" + id,
				data: {
					authcode: that.app.getAuthCode()
				},
				success: function(rs) {
					var res=rs.data;
					that.pageLoad = true;
					that.pageData = res.data;
					shopid=res.data.shop.shopid;
					that.ksList=res.data.ksList;
					that.ksList2=res.data.ksList2;
					that.ksid=res.data.ksid;
					that.ksid1=res.data.ksid;
					that.price=res.data.data.price;
					that.cart_amount=res.data.cart_amount;
				}
			})
		},
		dMinus:function(){
			if(this.cart_amount>1){
				this.cart_amount--;
			}
			
		},
		dPlus:function(){
			this.cart_amount++;
		},
		ks1:function(id){
			var that=this;
			uni.request({
				url:that.app.apiHost+"/module.php?m=b2b_product_ks&a=sizeList&ajax=1&id="+id,
				dataType:"json",
				data:{
					authcode:that.app.getAuthCode()
				},	
				success:function(rs){
					var res=rs.data;
					that.ksid1=res.data.ksid;
					that.ksid=res.data.ksid;
					that.ksproduct=res.data.product;
					that.price=res.data.ks.price;
					that.cart_amount=res.data.product.cart_amount;
					that.ksList2=res.data.ksList2;
				}
			})
		},
		ks2:function(id){
			var that=this;
			that.ksid=id;
			uni.request({
				url:that.app.apiHost+"/module.php?m=b2b_product_ks&a=get&ajax=1&id="+id,
				dataType:"json",
				data:{
					authcode:that.app.getAuthCode()
				},
				success:function(rs){
					var res=rs.data; 
					that.ksproduct=res.data.product;
					that.price=res.data.ks.price;
					that.cart_amount=res.data.product.cart_amount; 
				}
			 })
		},
		addCart: function() {
			var that = this; 
			var ksid=that.ksid;				 
			uni.request({
				url: that.app.apiHost+'/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: that.pageData.data.id,
					amount: that.cart_amount,
					ksid: ksid,
					shopid:shopid,
					authcode:that.app.getAuthCode()
				},
				method: 'GET',
				dataType: "json",
				success: function(rs) {
					var res=rs.data;
					if(res.error){
						uni.showToast({
							title:res.message
						})
						return false;
					}
					uni.showToast({
						title:"加入成功"
					})
					 
				}
			})
		},
		goBuy:function(){
			var that = this; 
			var ksid=that.ksid;				 
			uni.request({
				url: that.app.apiHost+'/module.php?m=b2b_cart&a=add&ajax=1',
				data: {
					productid: that.pageData.data.id,
					amount: that.cart_amount,
					ksid: ksid,
					shopid:shopid,
					authcode:that.app.getAuthCode()
				},
				method: 'GET',
				dataType: "json",
				success: function(rs) {
					var res=rs.data;
					if(res.error==1000){
						uni.navigateTo({
							url:"../login/index"
						});
						return false;
					}
					if(res.data.action=='delete'){
						return false;
					}
					uni.navigateTo({
						url:"../b2b_order/confirm?cartid="+res.data.cartid+"&shopid="+that.pageData.shop.shopid
					})
					 
				}
			})
		}
	}
}
